dl,
dt,
dd,
ol,
ul,
li {
  margin: 0;
  padding: 0;
}
.clear {
  clear: both;
  display: block;
  font-size: 0px;
  height: 0px;
  line-height: 0;
  width: 100%;
  overflow: hidden;
}
::selection {
  background: #F48A38;
  color: #FFF;
  text-shadow: none;
}
::-moz-selection {
  background: #F48A38;
  /* Firefox */
  color: #FFF;
  text-shadow: none;
}
::-webkit-selection {
  background: #F48A38;
  /* Safari */
  color: #FFF;
  text-shadow: none;
}
:active,
:focus {
  outline: none !important;
}
/* ----------------------------------------------------------------
	Typography
-----------------------------------------------------------------*/
body {
  line-height: 1.5;
  color: #555;
  font-size: 14px;
  font-family: 'Open sans', sans-serif;
}
a {
  text-decoration: none !important;
  color: #F48A38;
}
a:hover {
  color: #222;
}
a img {
  border: none;
}
img {
  max-width: 100%;
}
iframe {
  border: none !important;
}
/* ----------------------------------------------------------------
	Basic Layout Styles
-----------------------------------------------------------------*/
h1,
h2,
h3,
h4,
h5,
h6 {
  color: #444;
  font-weight: 600;
  line-height: 1.5;
  margin: 0 0 30px 0;
  font-family: 'Open sans', 'Raleway', sans-serif;
}
h5,
h6 {
  margin-bottom: 20px;
}
h1 {
  font-size: 36px;
}
h2 {
  font-size: 30px;
}
h3 {
  font-size: 24px;
}
h4 {
  font-size: 18px;
}
h5 {
  font-size: 14px;
}
h6 {
  font-size: 12px;
}
h4 {
  font-weight: 600;
}
h5,
h6 {
  font-weight: bold;
}
h1 > span:not(.nocolor),
h2 > span:not(.nocolor),
h3 > span:not(.nocolor),
h4 > span:not(.nocolor),
h5 > span:not(.nocolor),
h6 > span:not(.nocolor) {
  color: #F48A38;
}
p,
pre,
ul,
ol,
dl,
dd,
blockquote,
address,
table,
fieldset,
form {
  margin-bottom: 30px;
}
small {
  font-family: 'Open sans', sans-serif;
}
#wrapper {
  position: relative;
  float: none;
  width: 1220px;
  margin: 0 auto;
  background-color: #FFF;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
