  position: relative;
  padding: 0 !important;
  margin: 0;
  text-align: center;
}
.portfolio-overlay .portfolio-desc a {
  display: inline;
  position: relative;
  top: 0;
  left: 0;
  margin: 0;
  font-size: inherit;
  width: auto !important;
  height: auto !important;
  line-height: 1;
  background-color: transparent !important;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.15);
